let TintShader = {
    uniforms:
    {

        tDiffuse: { value: null },
        backgroundColor: { value: null },
        fadeStrength: { value: null },
        topFadeStrength: { value: null },
        bottomFadeStrength: { value: null },

    },
    vertexShader: `
        varying vec2 vUv;

        void main()
        {
            gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);

            vUv = uv;
        }
    `,
    fragmentShader: `
        uniform sampler2D tDiffuse;
        uniform vec4 backgroundColor;
        uniform float fadeStrength;
        uniform float topFadeStrength;
        uniform float bottomFadeStrength;

        varying vec2 vUv;

        void main()
        {
            vec4 color = texture2D(tDiffuse, vUv);
            //gl_FragColor = color;
            //gl_FragColor = mix(color,backgroundColor, fadeStrength);

            float smoothUVy = vUv.y * vUv.y * (3.0 - 2.0 * vUv.y);
            float t = mix(bottomFadeStrength, topFadeStrength, smoothUVy);
            gl_FragColor = mix(color, vec4(0.0, 0.0, 0.0, 0.0), fadeStrength);
            gl_FragColor = mix(color, vec4(0.0, 0.0, 0.0, 0.0), t);
        }
    `
}

const tintPass = new ShaderPass(TintShader)
tintPass.material.uniforms.backgroundColor.value = new THREE.Vector4(c1[0] / 255, c1[1] / 255, c1[2] / 255, 0.0)
let baseFadeStrength = 0.7;
tintPass.material.uniforms.fadeStrength.value = 0;
effectComposer.addPass(tintPass)


// Detect scroll position on the window
window.addEventListener('scroll', function () {
    // Get the scroll position
    const scrollTop = this.scrollY || document.documentElement.scrollTop;

    // Get the total height of the document
    const totalHeight = document.documentElement.scrollHeight - window.innerHeight;

    // Calculate the scroll ratio
    const scrollRatio = scrollTop / sizes.height;

    let fadingRange = 0.3
    let fadeStrength;
    let topFadeStrength;
    let bottomFadeStrength;
    if (scrollRatio < fadingRange) {
        fadeStrength = scrollRatio * baseFadeStrength / fadingRange
        bottomFadeStrength = scrollRatio * baseFadeStrength / fadingRange
        topFadeStrength = 0
    }
    else if (scrollRatio - fadingRange < fadingRange) {
        fadeStrength = baseFadeStrength;
        bottomFadeStrength = baseFadeStrength
        topFadeStrength = (scrollRatio - fadingRange) * baseFadeStrength / fadingRange
    }
    else if (totalHeight / sizes.height - scrollRatio < fadingRange) {
        fadeStrength = (totalHeight / sizes.height - scrollRatio) * baseFadeStrength / fadingRange
        bottomFadeStrength = 0
        topFadeStrength = (totalHeight / sizes.height - scrollRatio) * baseFadeStrength / fadingRange
    }
    else if (totalHeight / sizes.height - scrollRatio - fadingRange < fadingRange) {
        fadeStrength = baseFadeStrength;
        bottomFadeStrength = (totalHeight / sizes.height - scrollRatio - fadingRange) * baseFadeStrength / fadingRange
        topFadeStrength = baseFadeStrength;
    }
    else {
        fadeStrength = bottomFadeStrength = topFadeStrength = baseFadeStrength
    }
    // smooth
    fadeStrength = fadeStrength * fadeStrength * (3.0 - 2.0 * fadeStrength);
    bottomFadeStrength = bottomFadeStrength * bottomFadeStrength * (3.0 - 2.0 * bottomFadeStrength);
    topFadeStrength = topFadeStrength * topFadeStrength * (3.0 - 2.0 * topFadeStrength);

    tintPass.material.uniforms.fadeStrength.value = fadeStrength;
    tintPass.material.uniforms.bottomFadeStrength.value = bottomFadeStrength;
    tintPass.material.uniforms.topFadeStrength.value = topFadeStrength;

    // Use the scrollRatio value as needed
    // console.log('Scroll ratio:', scrollRatio);
    // console.log('a:', fadeStrength);

    // console.log(`top: ${topFadeStrength}, fade: ${fadeStrength}, bottom: ${bottomFadeStrength}, scrollRatioLeft: ${totalHeight / sizes.height - scrollRatio}`)
});

/**
 * Cursor
 */
const cursor = {}
cursor.x = 0
cursor.y = 0

function screenToWorld(cursor) {
    // Convert screen coordinates (in pixels) to normalized device coordinates (NDC)
    const mouse = new THREE.Vector3((cursor.x) * 2, -(cursor.y) * 2, 0);

    // Unproject mouse position to world coordinates
    mouse.unproject(camera);

    // Calculate direction from camera position to mouse position
    const dir = mouse.sub(camera.position).normalize();

    // Find intersection point with z = 0 plane
    const distance = -camera.position.z / dir.z;
    const worldPosition = camera.position.clone().add(dir.multiplyScalar(distance));

    return worldPosition;
}
let mouseWorldPos = new THREE.Vector3(0, 0, -5)
let mouseWorldPosPrev = new THREE.Vector3(0, 0, -5)



window.addEventListener('mousemove', (event) => {
    // if(isMobile) return
    cursor.x = event.clientX / sizes.width - 0.5
    cursor.y = event.clientY / sizes.height - 0.5
    // console.log(cursor)
})

window.addEventListener('touchstart', (event) => {
    cursor.x = event.touches[0].clientX / sizes.width - 0.5
    cursor.y = event.touches[0].clientY / sizes.height - 0.5
    mouseWorldPos = screenToWorld(cursor)
    mouseWorldPosPrev.copy(mouseWorldPos)
    // console.log(cursor)
})

window.addEventListener('touchmove', (event) => {
    cursor.x = event.touches[0].clientX / sizes.width - 0.5
    cursor.y = event.touches[0].clientY / sizes.height - 0.5
    // console.log(cursor)
})

/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0

const tick = () => {
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime


    const transformMatrix = halvorsen.object.matrixWorld.clone()
    const inverseMatrix = transformMatrix.invert()

    halvorsen.sphereMaterial.uniforms.parentTransformInverse.value = inverseMatrix
    if (halvorsen.isTrail) {

        halvorsen.trailMaterial.uniforms.parentTransformInverse.value = inverseMatrix
    }

    mouseWorldPos = screenToWorld(cursor)
    // console.log(worldPos)
    halvorsen.mouseWorldPos = mouseWorldPos
    halvorsen.mouseWorldPosPrev = mouseWorldPosPrev
    halvorsen.vel = new THREE.Vector3(
        mouseWorldPos.x - mouseWorldPosPrev.x,
        mouseWorldPos.y - mouseWorldPosPrev.y,
        mouseWorldPos.z - mouseWorldPosPrev.z,
    )
    halvorsen.UpdateHalvorsen(deltaTime)

    mouseWorldPosPrev.copy(mouseWorldPos)


    // Render
    // renderer.render(scene, camera)
    effectComposer.render()

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()